import { Button, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import axios from "axios";
import React, { useState } from "react";
import { NavLink, useNavigate, useParams } from "react-router-dom";

const Resetpassword = () => {
  // const href = window.location.href;
  const history = useNavigate();
  const params = useParams();
  const [loading, setLoading] = useState(null);

  const [newPass, SetnewPass] = useState("");
  const [controlPassword, setControlPassword] = useState("");
  const [errorPassword, setErrorPassword] = useState("");
  const [eye, seteye] = useState(true);

  const Eyeclick = () => {
    seteye(!eye);
    const code = document.getElementById("password");
    if (eye) {
      code.setAttribute("type", "password");
      seteye(false);
    } else {
      code.setAttribute("type", "text");
    }
  };
  const [request, setRequest] = useState(false);
  const sendRquest = async () => {
    const res = await axios
      .put(
        `${process.env.REACT_APP_URL_USER}/reset/${params.id}/verify/${params.token}`,

        {
          newPass,
        }
      )
      .catch((error) => {
        console.log(error);
        setErrorPassword(error.response.data.message);
        setLoading(false);
      });

    const data = await res.data;
    return data;
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setErrorPassword("");
    if (newPass !== controlPassword) {
      setErrorPassword("les mots de passe ne correspondent pas");
      setLoading(false);
    } else if (newPass.length <= 6) {
      setErrorPassword("Le Mot de passe est trop faible");
      setLoading(false);
    } else return sendRquest().then(() => setRequest(true));
  };
  return (
    <div>
      {request ? (
        <>{history("/")}</>
      ) : (
        <>
          <form method="post" onSubmit={handleSubmit}>
            <Box
              display="flex"
              flexDirection={"column"}
              maxWidth={400}
              alignItems={"center"}
              justifyContent={"center"}
              margin="auto"
              marginTop={7}
              padding={3}
              borderRadius={5}
              boxShadow={"1px 2px 6px 2px #ccc"}
            >
              <Typography variant="h6" padding={3} textAlign="center">
                Mettez un nouveau mot de passe.
              </Typography>

              <TextField
                margin="normal"
                id="password"
                type={"text"}
                variant="outlined"
                required
                label="Nouveau mot de passe"
                value={newPass}
                onChange={(e) => SetnewPass(e.target.value)}
              />
              <i
                onClick={Eyeclick}
                className={eye ? "fa-solid fa-eye-slash" : "fa-solid fa-eye"}
                style={{
                  position: "relative",
                  right: "-80px",
                  top: "-45px",
                  fontSize: "16px",
                  cursor: "pointer",
                }}
                aria-hidden="true"
                type="button"
                id="eye"
              ></i>
              <TextField
                margin="normal"
                type={"password"}
                variant="outlined"
                required
                label="Confirmez votre nouveau mot de passe"
                value={controlPassword}
                onChange={(e) => setControlPassword(e.target.value)}
              />
              <Typography variant="body1" color="error" id="error">
                {errorPassword}
              </Typography>
              {loading ? (
                <Button
                  disabled
                  type="submit"
                  sx={{ marginTop: 3, borderRadius: 1 }}
                  variant="contained"
                >
                  Envoyer
                </Button>
              ) : (
                <Button
                  type="submit"
                  sx={{ marginTop: 3, borderRadius: 1 }}
                  variant="contained"
                >
                  Envoyer
                </Button>
              )}

              <NavLink
                style={{
                  textAlign: "right",
                  margin: "15px",
                  position: "relative",
                  right: "-80px",
                  top: "15px",
                }}
                to="/"
              >
                Go to home
              </NavLink>
            </Box>
          </form>
        </>
      )}
    </div>
  );
};

export default Resetpassword;
