import { Box, Button, TextField, Typography } from "@mui/material";
import axios from "axios";
import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";

const SignIn = () => {
  const history = useNavigate();
  const [email, Setemail] = useState("");
  const [request, setRequest] = useState(false);
  const [password, Setpassword] = useState("");
  const [eye, seteye] = useState(true);
  const [erroremail, Seterroremail] = useState("");
  const [erroremailtwo, Seterroremailtwo] = useState("");
  const [loading, setLoading] = useState(null);

  const Eyeclick = () => {
    seteye(!eye);
    const code = document.getElementById("password");
    if (eye) {
      code.setAttribute("type", "password");
      seteye(false);
    } else {
      code.setAttribute("type", "text");
    }
  };

  const sendRquest = async () => {
    const res = await axios
      .post(
        `${process.env.REACT_APP_URL_USER}/login`,

        {
          email,
          password,
        }
      )
      .catch((error) => {
        //  console.log(error.response.data.message)

        Seterroremail("");
        Seterroremail(error.response.data.message);
      });

    const data = await res.data;
    return data;
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    if (!navigator.onLine) {
      setLoading(false);
      Seterroremail(
        "Connexion non active, vérifiez votre connexion puis réessayer!"
      );
    } else
      return sendRquest()
        .then(() => {
          history("/home");
        })
        .catch((error) => {
          Seterroremailtwo("");
          Seterroremailtwo(error);
          // setRequest(true);
          setLoading(false);
        });
  };
  return (
    <div>
      {request && loading === false ? (
        <Box
          display="flex"
          flexDirection={"column"}
          maxWidth={400}
          alignItems={"center"}
          justifyContent={"center"}
          margin="auto"
          marginTop={7}
          padding={3}
          borderRadius={5}
          boxShadow={"1px 2px 6px 2px #ccc"}
        >
          <CheckCircleIcon sx={{ fontSize: "38px", color: "green" }} />
          <Typography
            sx={{ color: "green" }}
            variant="h6"
            padding={3}
            textAlign="center"
          >
            Nous avons envoyé un e-mail à l'adresse enregistrée avec ce compte
            contenant des instructions supplémentaires pour récupérer votre mot
            de passe. Vous rencontrez toujours des difficultés pour vous
            connecter ? Veuillez contacter l'assistance.
          </Typography>
          <NavLink
            style={{
              textAlign: "right",
              margin: "15px",
              position: "relative",
              right: "-80px",
              top: "15px",
            }}
            to="/"
          >
            Revenir à l'accueil
          </NavLink>
        </Box>
      ) : (
        <form method="post" onSubmit={handleSubmit}>
          <Box
            display="flex"
            flexDirection={"column"}
            maxWidth={400}
            alignItems={"center"}
            justifyContent={"center"}
            margin="auto"
            marginTop={2}
            padding={3}
            borderRadius={5}
          >
            <Typography
              size="small"
              variant="h4"
              padding={3}
              textAlign="center"
            >
              Connectez vous
            </Typography>

            <TextField
              size="small"
              margin="normal"
              type={"email"}
              variant="outlined"
              required
              label="Email"
              value={email}
              onChange={(e) => Setemail(e.target.value)}
            />

            <TextField
              margin="normal"
              size="small"
              type={"text"}
              variant="outlined"
              required
              label="Mot de passe"
              id="password"
              value={password}
              onChange={(e) => Setpassword(e.target.value)}
              sx={{ marginTop: "40px" }}
            />

            <i
              style={{
                position: "relative",
                right: "-80px",
                top: "-40px",
                fontSize: "14px",
                cursor: "pointer",
              }}
              onClick={Eyeclick}
            >
              {eye ? (
                <VisibilityOffIcon size="small" />
              ) : (
                <RemoveRedEyeIcon size="small" />
              )}
            </i>

            {erroremail.length >= 1 && (
              <Box boxShadow={"1px 1px 1px 1px #ccc"}>
                <Typography margin={1} variant="body1" color="error" id="error">
                  {erroremail}
                </Typography>
              </Box>
            )}
            {erroremailtwo.length >= 1 && (
              <Box boxShadow={"1px 1px 1px 1px #ccc"}>
                <Typography margin={1} variant="body1" color="error" id="error">
                  {erroremailtwo}
                </Typography>
              </Box>
            )}

            {loading ? (
              <Button
                disabled
                type="submit"
                sx={{ marginTop: 3, borderRadius: 1 }}
                variant="contained"
              >
                Se connecter{" "}
              </Button>
            ) : (
              <Button
                type="submit"
                sx={{ marginTop: 3, borderRadius: 1 }}
                variant="contained"
              >
                Se connecter{" "}
              </Button>
            )}

            <NavLink
              style={{
                textAlign: "right",
                margin: "15px",
                position: "relative",
                right: "-80px",
                top: "15px",
              }}
              to="/forget_password"
            >
              Mot de passe oublié ?
            </NavLink>
          </Box>
        </form>
      )}
    </div>
  );
};

export default SignIn;
