/* eslint-disable array-callback-return */
import React, { useEffect, useState } from "react";
import axios from "axios";
import Navbar from "../../navigation/navbar";
import { useDispatch, useSelector } from "react-redux";
import Notfound from "../notfound";
import { getAllPost } from "../../../features/post.reducers";
import Listpost from "./listpost";
import { setgetusers } from "../../../features/users.reducers";
import Postform from "../profil/postform";
import "../../styles/home.scss";

import Skeleton from "../../skeleton/Skeleton";
import { setgetUsers } from "../../../features/user.reducers";

axios.defaults.withCredentials = true;
const Home = () => {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user.user);
  const allPosts = useSelector((state) => state.posts.posts);

  const [loadPost, setloadPost] = useState(true);
  const [count, setcount] = useState(10);
  const [isChoice, setIschoice] = useState(false);
  const [isChoiceQuartier, setIschoiceQuartier] = useState(false);
  const getUserWithVerifyToken = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}/jwt`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };

  // Récuperer touts les utilisateurs
  const sendRquestUsers = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_USER}`, {
      withCredentials: true,
    });
    // .catch((err) => console.log(err));
    const data = await res.data;
    return data;
  };
  // Recuperer touts les posts
  const sendRquestPost = async () => {
    const res = await axios.get(`${process.env.REACT_APP_URL_POST}`, {
      withCredentials: true,
    });
    // .catch(err => console.log(err));
    const data = await res.data;
    return data;
  };
  // const handleChoice = () => {
  //   setIschoice(true);
  //   setIschoiceQuartier(false);
  // };
  // const handleChoiceFirst = () => {
  //   setIschoice(false);
  //   setIschoiceQuartier(false);
  // };
  // const handleChoiceQuartier = () => {
  //   setIschoice(true);
  //   setIschoiceQuartier(true);
  // };
  //Au plus bas de la page recharger

  const loadMore = () => {
    if (
      window.innerHeight + document.documentElement.scrollTop + 1 >
      document.scrollingElement.scrollHeight
    ) {
      setloadPost(true);
    }
  };
  useEffect(() => {
    getUserWithVerifyToken().then((data) => {
      dispatch(setgetUsers(data.user));
    });
  },[]);
  useEffect(() => {
    sendRquestUsers().then((data) => {
      dispatch(setgetusers(data));
      //  console.log(data);
    });
  }, [dispatch]);
  useEffect(() => {
    if (loadPost) {
      sendRquestPost().then(() => {
        dispatch(getAllPost());
      });

      setcount(count + 5);
      setloadPost(false);
    }
    window.addEventListener("scroll", loadMore);
    return () => window.removeEventListener("scroll", loadMore);
  }, [loadPost, dispatch, count]);

  return (
    <>
      {user ? (
        <>
          <Navbar />
          <>
            <Postform />
          </>

          {allPosts ? (
            isChoice === false &&
            allPosts?.slice(0, count).map(function (el) {
              return (
                <ul>
                  <Listpost el={el} />
                </ul>
              );
            })
          ) : (
            <Skeleton />
          )}
          {isChoice &&
            allPosts
              ?.filter((el) => {
                if (
                  el.activitePost
                    ?.toLocaleLowerCase()
                    .includes(user.activite?.toLocaleLowerCase())
                ) {
                  return true;
                }
              })
              .slice(0, count)
              .map((el, index) => {
                return (
                  <ul>
                    <Listpost el={el} cle={index} />
                  </ul>
                );
              })}
          {isChoice === false &&
            isChoiceQuartier &&
            allPosts
              ?.filter((el) => {
                if (
                  el.activitePost
                    ?.toLocaleLowerCase()
                    .includes(user.activite?.toLocaleLowerCase()) &&
                  el.quartierPost
                    ?.toLocaleLowerCase()
                    .includes(user.quartier?.toLocaleLowerCase())
                ) {
                  return true;
                }
              })
              .slice(0, count)
              .map((el, index) => {
                return (
                  <ul>
                    <Listpost el={el} cle={index} />
                  </ul>
                );
              })}
        </>
      ) : (
        <>
          <Notfound />
        </>
      )}
    </>
  );
};

export default Home;
