import CheckCircle from "@mui/icons-material/CheckCircle";
import { Box, Typography } from "@mui/material";
import axios from "axios";
import React, { useEffect } from "react";

import { NavLink, useNavigate, useParams } from "react-router-dom";

const VeriyLink = () => {
  const params = useParams();
  const history = useNavigate();

  const sendRquest = async () => {
    const res = await axios
      .get(
        `${process.env.REACT_APP_URL_USER}/active/${params.id}/verify/${params.token}`
      )
      .catch((error) => console.log(error));
    const data = await res.data;
    return data;
  };
  useEffect(() => {
    sendRquest().then((res) => {
      history("/home");
      console.log(res);
    });
    // .catch((error) => {
    //   if (error.response.data.message.includes("Le lien est invalid")) {
    //     setSuccess("lien invalid");
    //   }
    // });
  });
  console.log(params.id, params.token);
  return (
    <Box
      display="flex"
      flexDirection={"column"}
      maxWidth={400}
      alignItems={"center"}
      justifyContent={"center"}
      margin="auto"
      marginTop={7}
      padding={3}
      borderRadius={5}
      boxShadow={"1px 2px 6px 2px #ccc"}
    >
      <CheckCircle sx={{ fontSize: "38px", color: "green" }} />
      <Typography
        sx={{ color: "red" }}
        variant="h6"
        padding={3}
        textAlign="center"
      >
        Le lien à problablement expirer ou à déja été cliquer. Nous vous prions
        de vous connecter pour recevoir un autre mail de vérification si celui
        n'est plus valable.
      </Typography>
      <NavLink
        style={{
          textAlign: "right",
          margin: "15px",
          position: "relative",
          right: "-80px",
          top: "15px",
        }}
        to="/"
      >
        Revenir à l'accueil
      </NavLink>
    </Box>
  );
};

export default VeriyLink;
